<template>
  <section class="hero is-fullheight is-light">
    <div class="hero-head">
      <div class="header">
        <nav class="nav">
          <div class="container">
            <div class="nav-left">
              <router-link class="nav-item is-brand" to="/index">
                <img src="../assets/logo.png" alt="E">
              </router-link>
            </div>
            <span class="nav-toggle" :class="{'is-active': device.isMobile && menu.opened}" @click="toggleMenu(!menu.opened)">
              <span></span>
              <span></span>
              <span></span>
            </span>
            <div class="nav-right nav-menu" :class="{'is-active': device.isMobile && menu.opened}">
              <span class="nav-item is-tab">
                <router-link class="" to="/signup"></router-link>
              </span>
              <span class="nav-item is-tab">
                <router-link class="button is-primary" to="/signin"></router-link>
              </span>
            </div>
          </div>
        </nav>
      </div>
    </div>
    <div class="hero-body">
      <div class="container">
        <div class="columns is-vcentered">
          <div class="column is-10 is-offset-1">
            <div class="columns section" v-if="isSuccess==false">
              <form class="column is-6 is-offset-3" @submit.prevent="validateForm">
                <h1 class="title has-text-centered"></h1>
                <div class="field">
                  <label class="label">* </label>
                  <p class="control">
                    <input name="name" class="input" :class="{'is-danger': errors.has('name')}" type="text" v-model="userForm.PanelLoginName" v-validate="'required'" placeholder="">
                    <span class="help is-danger" v-show="errors.has('name')">{{ errors.first('name') }}</span>
                  </p>
                </div>
                <div class="field">
                  <label class="label">* </label>
                  <p class="control">
                    <input name="password" class="input" :class="{'is-danger': errors.has('password')}" type="password" v-model="userForm.PanelPw" v-validate="'required'" placeholder="">
                    <span class="help is-danger" v-show="errors.has('password')">{{ errors.first('password') }}</span>
                  </p>
                </div>
                <div class="field">
                  <label class="label">* </label>
                  <p class="control">
                    <input name="rePassword" :class="{'is-danger': errors.has('rePassword')}" v-model="userForm.RPanelPw" v-validate="'required'" type="password" class="input" placeholder="">
                    <span class="help is-danger" v-show="errors.has('password')">{{ errors.first('rePassword') }}</span>
                  </p>
                </div>
                <div class="field">
                  <label class="label">* </label>
                  <p class="control">
                    <input name="email" :class="{'is-danger': errors.has('email')}" v-model="userForm.PanelEmail" v-validate="'required'" type="email" class="input" placeholder="">
                    <span class="help is-danger" v-show="errors.has('email')">{{ errors.first('email') }}</span>
                  </p>
                </div>
                <div class="field">
                  <label class="label"></label>
                  <p class="control">
                    <input type="text" v-model="userForm.PanelRealName" class="input " placeholder="">
                  </p>
                </div>
                <div class="field">
                  <label class="label">* </label>
                  <p class="control">
                    <span class="select">
                      <select :class="{'is-danger': errors.has('year')}" name="year" v-model="ageVal.year" v-validate="'required'" @change="changeAge('year')">
                        <option value=""></option>
                        <option :value="item" v-for="(item, index) in ageOptons.year">{{item}}</option>
                      </select>
                    </span>
                    <span class="select">
                      <select :class="{'is-danger': errors.has('month')}" name="month" v-model="ageVal.month" v-validate="'required'" @change="changeAge('month')">
                        <option value=""></option>
                        <option :value="item" v-for="(item, index) in ageOptons.month">{{item}}</option>
                      </select>
                    </span>
                    <span class="select">
                      <select :class="{'is-danger': errors.has('day')}" name="day" v-model="ageVal.day" v-validate="'required'">
                        <option value=""></option>
                        <option :value="item" v-for="(item, index) in ageOptons.day">{{item}}</option>
                      </select>
                    </span>
                  </p>
                </div>
                <div class="field">
                  <label class="label">* </label>
                  <p class="control">
                    <!--<input type="text" name="sex" :class="{'is-danger': errors.has('sex')}" v-model="userForm.PanelSex" v-validate="'required'" class="input" placeholder="">-->
                    <radio-group v-model="userForm.PanelGender">
                      <radio val="1"></radio>
                      <radio val="2"></radio>
                    </radio-group>
                  </p>
                </div>
                <div class="field">
                  <label class="label">* </label>
                  <p class="control">
                    <span class="select" style="width:150px">
                      <select :class="{'is-danger': errors.has('Panelprovince')}" name="Panelprovince" v-validate="'required'" v-model="userForm.Panelprovince" @change="queryByParenterCode(userForm.Panelprovince)">
                        <option value=""></option>
                        <option :value="item.value" v-for="(item, index) in provinceOptions">{{item.label}}</option>
                      </select>
                    </span>
                    <span class="select" style="width:150px">
                      <select :class="{'is-danger': errors.has('PanelCity')}" name="PanelCity" v-model="userForm.PanelCity" v-validate="'required'">
                        <option value=""></option>
                        <option :value="item.value" v-for="(item, index) in cityOptions">{{item.label}}</option>
                      </select>
                    </span>
                    <!--<span class="help" v-show="errors.has('PanelCity')">{{ errors.first('PanelCity') }}</span>-->
                  </p>
                </div>
                <div class="field">
                  <label class="label">* </label>
                  <!--<p class="control">
                      <input name="mobile" :class="{'is-danger': errors.has('mobile')}" v-model="userForm.PanelMobile" v-validate="'required'" type="number" class="input" placeholder="">
                      <span class="help is-danger" v-show="errors.has('mobile')">{{ errors.first('mobile') }}</span>
                    </p>-->
                    <div class="control">
                      <div class="control has-addons">
                        <input class="input is-expanded" name="mobile" type="number" placeholder="" v-model="userForm.PanelMobile" v-validate="'required'" :class="{'is-danger': errors.has('mobile')}">
                        <button class="button is-primary" type="button" :disabled="num>0" @click="checkMobile" v-text="btnText"></button>
                      </div>
                    </div>
                    <span class="help is-danger" v-show="errors.has('mobile')">{{ errors.first('mobile') }}</span>
                </div>
  
                <div class="field">
                  <label class="label">* </label>
                  <p class="control">
                    <input type="text" v-model="userForm.MobileCode" name="ValidateCode" class="input" :class="{'is-danger': errors.has('ValidateCode')}" placeholder="" v-validate="'required'">
                    <span class="help is-danger" v-show="errors.has('ValidateCode')">{{ errors.first('ValidateCode') }}</span>
                  </p>
                </div>
  
                <div class="field">
                  <label class="checkbox">
                    <input name="check" v-validate="'required'" type="checkbox" checked> 
                  </label>
                  <a class="agreement-btn" @click="showAgreement=true"></a>
                  <span class="help is-danger" data-vv-as="" v-show="errors.has('check')"></span>
                </div>
                <div class="field">
                  <label class="label"></label>
                  <p class="control">
                    <textarea type="text" row="3" v-model="userForm.PanelRemark" class="textarea" placeholder="" />
                  </p>
                  <p class="control">
                    <button class="button is-primary is-fullwidth is-medium" type="submit"></button>
                  </p>
                </div>
              </form>
            </div>
            <div v-else class="section msg-box has-text-centered">
              <div class="success-heading">
                <i class="iconfont icon-chenggong1 icon is-success"></i>
                <h2 class="title"></h2>
              </div>
              <div class="content">
                <p class="desc">24</p>
                <p>
                  <router-link class="button is-outlined is-medium" to="/signin"></router-link>
                  <a :href="getEmailUrl()" class="button is-primary is-success is-medium" target="_blank"></a>
                </p>
              </div>
            </div>
          </div>
        </div>
      </div>
  
    </div>
    <div class="hero-foot">
      <footer class="footer">
        <div class="container">
          <div class="content has-text-centered">
            Copyright  2016 EQYING Inc. All Rights Reserved ICP14016765-2
          </div>
        </div>
      </footer>
    </div>
    <modal title="" :is-show="showAgreement" transition="fadeDown" @close="showAgreement=false">
      <div class="content">
  
        <h4 class="has-text-centered">E</h4>
        <p>E</p>
  
        <p>1.1 </p>
  
        <p>1.2 </p>
  
        <p>2.1 </p>
  
        <p>2.2 </p>
  
        <p>2.3 ()()
        </p>
        <p>3.1 
          <ul>
            <li>(1)</li>
            <li>(2)</li>
          </ul>
          </p>
  
        <p>3.2 
          <ul>
            <li>(1)</li>
            <li>(2)</li>
            <li>(3)</li>
            <li>(4)</li>
          </ul>
        </p>
        <p>
          3.3 </p>
  
        <p>3.4 </p>
        <p>3.5 </p>
  
        <p>4.1 ([2004]1)</p>
  
        <p>4.2 
          <ul>
            <li>(1)</li>
            <li>(2)</li>
            <li>(3)</li>
            <li>(4)</li>
            <li>(5)</li>
            <li>(6)</li>
            <li>(7)</li>
            <li>(8)</li>
            <li>(9)</li>
            <li>(10)</li>
            <li>(11)</li>
          </ul>
        </p>
  
        <P>4.3 ()()</P>
  
        <p>4.4 
          <ul>
            <li>(1)</li>
            <li>(2)</li>
            <li>(3)</li>
            <li>(4)</li>
          </ul>
        </p>
  
        <p>4.5 ()
        </p>
        <p>5.1 
          <ul>
            <li>(1)</li>
            <li>(2)</li>
            <li>(3)</li>
            <li>(4)</li>
          </ul>
        </p>
  
        <p>5.2 </p>
        <p>5.3 </p>
        <p>
          6.1 </p>
        <p>
          6.2 </p>
  
        <p>6.3 </p>
        <p>
          6.4 "E(www.eqiying.com)"</p>
        <p>
          6.5 </p>
        <p>
          6.6 </p>
  
        <p>6.7 </p>
        <p>
          7.1 </p>
  
        <p>7.2 </p>
  
        <p>7.3 </p>
  
        <p>7.4 </p>
  
        <p>7.5 
          <ul>
            <li>(1)</li>
            <li>(2)</li>
            <li>(3)</li>
          </ul>
        </p>
        <p>7.6 ()</p>
        <p>8.1 </p>
        <p>8.2 </p>
        <p>8.3 </p>
      </div>
      <div slot="footer">
        <a class="button is-primary" @click="showAgreement=false"></a>
      </div>
    </modal>
  </section>
</template>

<script>
import api from 'src/api/index.js'
import { mapActions, mapGetters } from 'vuex'

export default {
  // name: 'signup',
  data() {
    return {
      isSuccess: false,
      num: 0,
      userForm: {
        PanelLoginName: '',
        PanelPw: '',
        RPanelPw: '',
        PanelEmail: '',
        PanelCode: '',
        PanelRealName: '',
        PanelRemark: '',
        PanelWebId: '1',
        Panelprovince: '',
        PanelCity: '',
        PanelGender: '1',
        PanelBirthday: '',
        PanelMobile: ''
      },
      provinceOptions: [],
      cityOptions: [],
      ageOptons: {
        year: [],
        month: [],
        day: []
      },
      ageVal: {
        year: '1985',
        month: '1',
        day: '1'
      },
      showAgreement: false
    }
  },
  computed: {
    ...mapGetters({
      current: 'current',
      device: 'device',
      menu: 'menu'
    }),
    btnText() {
      return this.num === 0 ? '' : this.num + ''
    }
  },
  created() {
    this.queryByParenterCode('100000')

    const year = new Date().getFullYear() + 1
    for (let i = year - 99; i < year; i++) {
      this.ageOptons.year.push(i + '')
    }
    for (let i = 1; i < 13; i++) {
      this.ageOptons.month.push(i + '')
    }

    this.changeAge('month')
  },
  methods: {
    ...mapActions([
      'toggleMenu'
    ]),
    getEmailUrl() {
      const { PanelEmail } = this.userForm
      return 'http://mail.' + PanelEmail.split('@')[1]
    },
    validateForm() {
      this.$validator.validateAll().then(() => {
        console.log(this.ageVal)
        const { year, month, day } = this.ageVal

        this.userForm.PanelBirthday = parseInt(year) + '-' + parseInt(month) + '-' + parseInt(day)

        const {inviteUser, misc} = this.$route.query

        if(inviteUser){
          this.userForm.inviteUser = inviteUser
        }

        if(misc){
          this.userForm.misc = misc
        }

        api.signup({ ...this.userForm }).then(res => {
          const { msg, result } = res.data;
          if (result !== 'ok') {
            this.$notify.warning({ content: msg || '' });
            return false;
          }

          // this.$notify.success({ content: msg || '' });
          // this.$router.push({ path: '/signin' })
          this.isSuccess = true
        }).catch((error) => {
          console.log(error)
        })
      })
    },
    queryByParenterCode(ParenterCode) {
      api.queryByParenterCode({ ParenterCode }).then(res => {
        const { msg, result, data } = res.data
        if (result !== 'ok') {
          this.$notify.warning({ content: msg || '' });
          return false;
        }

        const options = data.map(item => {
          return {
            value: item.ParamsValues,
            label: item.ParamsName
          }
        })
        // this.provinceOptions = 
        if (ParenterCode === '100000') {
          this.provinceOptions = options
          this.userForm.PanelCity = ''
        } else {
          this.cityOptions = options
        }
      })
    },
    changeAge(type) {
      if (type === 'month') {
        this.ageOptons.day = []
        // this.ageVal.day = ''
        const { year, month } = this.ageVal
        const dayLength = new Date(parseInt(year), parseInt(month), 0).getDate() + 1
        for (let i = 1; i < dayLength; i++) {
          this.ageOptons.day.push(i + '')
        }
      }
    },
    checkMobile() {
			const { userForm: { PanelMobile } } = this

      if(!PanelMobile){
        // this.errors.add('mobile', '')
        return false
      }

			api.checkUnique({
				TypeId: '1',
				strWord: PanelMobile
			}).then(res => {
				const { msg, result, OK } = res.data
				if (result !== 'ok' && result !== 'NotExist') {
					this.$notify.warning({ content: '' });
					return false;
				}

				this.sendMsg()
			})
		},
    sendMsg() {
			this.num = 60
			const timer = setInterval(() => {
				if (this.num === 0) {
					clearInterval(timer)
				} else {
					this.num--
				}
			}, 1000)

			const { PanelMobile } = this.userForm
			api.sendSms({ PanelMobiles: PanelMobile}).then(res => {
				const { msg, result, OK } = res.data

				if (OK || result == 1) {
					this.$notify.success({ content: '' })
				} else {
					this.$notify.warning({ content: '' })
				}
			}).catch((error) => {
				this.$notify.warning({ content: '' })
			})
		}
  }
}
</script>

<style lang="scss" scoped>
.field {
  margin-bottom: 15px
}

.select {
  select {
    width: 100%
  }
}

.agreement {
  &-btn {
    position: relative;
    top: -2px
  }
}
</style>



